@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@font-face {
  font-family: "Fontawesome Brands";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeBrands.ttf") format("truetype");
}
@font-face {
  font-family: "Fontawesome Duotone";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeDuotone.ttf") format("truetype");
}
@font-face {
  font-family: "Fontawesome Light";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeLight.ttf") format("truetype");
}
@font-face {
  font-family: "Fontawesome Regular";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeRegular.ttf") format("truetype");
}
@font-face {
  font-family: "Fontawesome Solid";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeSolid.ttf") format("truetype");
}
@font-face {
  font-family: "Fontawesome Thin";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/fontawesome/fontawesomeThin.ttf") format("truetype");
}
:root, input, button, textarea, select {
  font-size: 16px;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  margin: 0;
  padding: 0;
  inset: 0;
  position: absolute;
  box-sizing: border-box;
  overflow: clip;
}

ol, ul {
  list-style: none;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-style: solid;
  border-width: 0;
  border-color: transparent;
  outline-style: solid;
  outline-width: 0;
  outline-color: transparent;
  font-family: Montserrat;
  transition: all 118ms ease-in-out;
}

[intention=default], [intention=neutral] {
  --color: theme("colors.neutral.400");
  --color-50: theme("colors.neutral.50");
  --color-100: theme("colors.neutral.100");
